import React from 'react';
import './../index.css';

const Experience = () => {
    return (
        <div className='d-flex justify-content-center text-center' style={{ marginTop: "6rem" }}>
            <div style={{ width: "35rem" }}>
                <div className="rounded shadow-sm py-5 px-4" style={{ border: '1px solid green', backgroundColor: 'rgb(31, 29, 29)' }}>
                    <h5 className="mb-0 color">Work Experience</h5>
                    <p className="textColor">
                        Worked extensively with Java 8’s time API in order to fix improperly entered
                        restaurant open times by restaurant owners. Worked on adding authentication to the api’s used in the backend. This helped
                        us ensure only authenticated users can use the API’s, and helped identify bad
                        actors misusing the apis. Spent other times monitoring papertrail, and memcached logs. Participated in code reviews for backend and frontend code, and also involved
                        in team sprint meetings.
                    </p>
                </div>

            </div>
        </div>
    )
}

export default Experience;