                    <img src="https://res.cloudinary.com/devevangel/image/upload/c_fill,h_449,w_449/v1603645505/LegacyEA/videos/_MG_7354_nkqm52.jpg" alt="" width="100" className="img-fluid rounded-circle mb-3 img-thumbnail shadow-sm" />
